//HEADER-START
.header {
  height: 100vh;
  background-color: #1f1f20;
  color: #fff;
  position: relative;

  .container {
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    right: 0;
  }
}

.particles {
  position: absolute;
  width: 100%;
  height: 100%;
}

.header__inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}

.header__discription {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  min-height: 207px;
  height: 100%;


  span {
    font-family: 'Poppins', sans-serif;
    font-size: 16px;
    font-weight: 400;
  }

  h2, h2 > span {
    font-weight: 400;
    font-family: 'Josefin Sans', sans-serif;
    font-size: 50px;
    line-height: 53px;
  }

  .name {
    position: relative;
    z-index: 10;

    &::after {
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 20px;
      background-color: #4E93E6;
      z-index: -1;
    }
  }


  h3 span {
    font-size: 27px;
    font-family: 'Poppins', sans-serif;
    font-weight: 700;
  }

}

.header__photo {
  position: relative;
  z-index: 0;

  img {
    min-height: 430px;
    max-width: 300px;
  }

  .border {
    height: 450px;
    width: 358px;
    position: absolute;
    top: -10px;
    right: -60px;
    border: 5px solid #4E93E6;
    z-index: -1;
  }
}

.work {
  height: 500px;
  background-color: #efefef;
}

//HEADER-END

.hamburger {
  position: absolute;
  width: 2.3rem;
  height: .13rem;
  border-radius: 0;
  background-color: white;
  display: block;
  margin: 0px;
  left: 40px;
  bottom: 60px;
  transition: 400ms;
}

.hamburger:before {
  position: absolute;
  width: 1.3rem;
  height: .13rem;
  border-radius: 2px;
  background-color: white;
  display: block;
  margin: auto;
  transition: 400ms;
  content: '';
  backface-visibility: hidden;
  transform: rotate(0deg) translateY(10px);
  right: 0;
}

.hamburger:after {
  position: absolute;
  width: 2.3rem;
  height: .13rem;
  border-radius: 2px;
  background-color: white;
  display: block;
  margin: auto;
  transition: 400ms;
  content: '';
  backface-visibility: hidden;
  transform: rotate(0deg) translateY(-10px);
}

.hamburger-overlay {
  box-shadow: 0 0 0 0 #1f1f20;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  transition: .5s ease-in-out;
  border-radius: 50%;
}

.hamburger-menu.open .hamburger-overlay {
  box-shadow: 0 0 0 120vw rgba(31, 31, 32, 1), 0 0 0 120vh rgba(31, 31, 32, 1);
  opacity: 0.9
  /*border-radius: 0;*/
}

.hamburger-menu {
  position: fixed;
  right: -100px;
  top: -100px;
  z-index: 100;
  width: 200px;
  height: 200px;
  background-color: rgba(31, 31, 32, .9);
  border-radius: 50% 50% 50% 50%;
  transition: .5s ease-in-out;
  cursor: pointer;
  border: none;
}

.hamburger-menu.open .hamburger:before {
  transform: rotate(45deg) translateY(0px);
  width: 2.3rem;
}

.hamburger-menu.open .hamburger:after {
  transform: rotate(-45deg) translateY(0px);
}

.hamburger-menu.open .hamburger {
  background-color: rgba(255, 255, 255, 0);
}

.Menu-list {
  font-size: 50px;
  line-height: 1.2;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
}

.Menu-list-item a {
  color: transparent !important;
  font-family: 'Josefin Sans', sans-serif;
}

.Menu-list-item {
  position: relative;
  color: transparent;
  cursor: pointer;
}

.Menu-list-item::before {
  content: '';
  display: block;
  position: absolute;
  top: 49%;
  left: -10%;
  right: -10%;
  height: 4px;
  border-radius: 4px;
  margin-top: -2px;
  background: #4e93e6;
  transform: scale(0);
  transition: transform 0.8s cubic-bezier(0.16, 1.08, 0.38, 0.98);
  z-index: 1;
}

.menu-wrapper .Menu {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  position: fixed;
  left: 0;
  right: 0;
  width: 50%;
  margin: 0 auto;
  z-index: 11111;
  opacity: 0;
  visibility: hidden;
  transition: all 0.8s cubic-bezier(0.16, 1.08, 0.38, 0.98);
}

.menu-wrapper.open .Menu {
  opacity: 1;
  visibility: visible;
}

.Mask {
  display: block;
  position: absolute;
  overflow: hidden;
  color: #4e93e6;
  top: 0;
  height: 49%;
  transition: all 0.8s cubic-bezier(0.16, 1.08, 0.38, 0.98);
}

.Mask span {
  display: block;
}

.Mask + .Mask {
  top: 48.9%;
  height: 51.1%;
}

.Mask + .Mask span {
  -webkit-transform: translateY(-53%);
  transform: translateY(-53%);
}

.Menu-list-item:hover .Mask,
.Menu-list-item:active .Mask {
  color: #FFF;
  -webkit-transform: skewX(12deg) translateX(5px);
  transform: skewX(12deg) translateX(5px);
}

.Menu-list-item:hover .Mask + .Mask,
.Menu-list-item:active .Mask + .Mask {
  -webkit-transform: skewX(12deg) translateX(-5px);
  transform: skewX(12deg) translateX(-5px);
}

.Menu-list-item:hover::before,
.Menu-list-item:active::before {
  -webkit-transform: scale(1);
  transform: scale(1);
}


.contact {
  background-color: #1F1F20;
  padding: 100px 0;
}

.titleSection {
  margin: 0 auto;
  text-align: center;
  font-size: 36px;
  font-family: 'Josefin Sans', sans-serif;
  text-transform: capitalize;
  font-weight: 400;
  color: white;
  position: relative;

  &::after {
    content: "";
    position: absolute;
    bottom: -60px;
    left: 0;
    right: 0;
    background: #4e93e6;
    width: 55px;
    height: 1px;
    margin: 30px auto;
  }

}

.contactForm {
  margin: 0 auto;
  margin-top: 100px;
  width: 585px;

  input {
    margin: 15px 0;
    display: block;
    width: 100%;
    height: 32px;

  }

  textarea {
    resize: none;
  }

  button {
    margin: 0 auto;
    margin-top: 30px;
    text-align: center;
    display: block;
    cursor: pointer;
    font: 400 14px/1.2 'Poppins', sans-serif;
    text-transform: uppercase;
    background: transparent;
    color: white;
    border: none;
    padding: 0 10px;
    transition: all 0.4s ease-in-out 0s;
    position: relative;
    height: 30px;
    z-index: 22;

    &:after {
      content: "";
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      height: 10px;
      width: 50%;
      background: #4e93e6;
      z-index: -1;
      transition: all 0.2s ease-in-out 0s;
      margin: 0 auto;

    }

    &:hover {
      &:after {
        width: 100%;
        height: 100%;
      }
    }
  }
}


.formContorl {
  font-size: 12px;
  border: 1px solid #495057;
  transition: all .3s;
  background-color: #252527;
  outline: none;
  width: 100%;
  height: 158px;
  padding: 15px;
  color: white;
  font-weight: 400;
  line-height: 1.5;
  white-space: pre-wrap;
  font-family: 'Poppins', sans-serif;

  &:focus {
    border: 1px solid #252527;
    color: #495057;
  }
}


@media (max-width: 790px) {
  .header__inner {
    flex-wrap: wrap;
    justify-content: center;
  }
  .header__item {
    margin-bottom: 40px;
  }
  .header__photo img {
    min-height: 100%;
    max-width: 270px;
  }
  .header__photo .border {
    height: 100%;
    width: 100%;
    top: -17px;
  }
  .header__discription {
    min-height: 167px;
  }
}

@media (max-width: 640px) {
  .contactForm {
    width: 100%;
  }
}

@media (max-width: 312px) {
  .header__discription h2 > span {
    font-size: 43px;
    line-height: 41px;
  }
  .header__photo img {
    max-width: 240px;
  }
  .header__discription h3 span {
    font-size: 20px;
  }
}

